	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>AI Banking is a project exploring how artificial intelligence might be integrated into online banking services. Asking questions along the lines of personal connection and efficiency towards the millennial generation. AI banking provides an environment in which user’s with low financial literacy can be faced with an enriching personal experience when it comes to banking — an otherwise difficult topic for the millennial generation. AI banking does so by focusing on a dynamic savings model, and the harnessing the power of AI/ data driven design for efficient banking advice.</p>\n<p>The full case study will be available at <a href=\"http://kowalskiforged.com/\">kowalskiforged.com</a></p>","frontmatter":{"title":"AI Banking","graduate":"John Kowalski","category":"UI/UX","category2":"Product Design","category3":"Experiential Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/ai_cover-b3afebd19c06995a4ebebff9ff923ff4-7b65b.jpg","srcSet":"/the-next-website/static/ai_cover-b3afebd19c06995a4ebebff9ff923ff4-05553.jpg 375w,\n/the-next-website/static/ai_cover-b3afebd19c06995a4ebebff9ff923ff4-ee37a.jpg 750w,\n/the-next-website/static/ai_cover-b3afebd19c06995a4ebebff9ff923ff4-7b65b.jpg 1044w","sizes":"(max-width: 1044px) 100vw, 1044px"}}}}},"projectsInCategory":{"edges":[{"node":{"frontmatter":{"title":"AI Banking","graduate":"John Kowalski"}}},{"node":{"frontmatter":{"title":"ARSight: A wearable augmented reality system for the visually impaired","graduate":"Michael Calcada"}}}]},"projectByStudent":{"edges":[{"node":{"frontmatter":{"title":"AI Banking","graduate":"John Kowalski"}}}]}},"pathContext":{"slug":"/work/ai-banking/","name":"/AI Banking/","title":"AI Banking","graduate":"/John Kowalski/","category":"/UI/UX/"}}

